/* Button */

input[type="button"],
input[type="submit"],
input[type="reset"] {
    -webkit-transition: background-color 0.35s ease-in-out, color 0.35s ease-in-out;
    transition: background-color 0.35s ease-in-out, color 0.35s ease-in-out;
    -webkit-appearance: none;
    position: relative;
    display: inline-block;
    border: 0;
    background: #a00000;
    color: #fff;
    text-shadow: 0 0 0.5px rgba(255, 255, 255, 0.25);
    cursor: pointer;
    text-decoration: none;
    outline: 0;
    padding: 1em 3em 1em 3em;
    text-align: center;
    border-radius: 3em;
    font-weight: 400;
}

    input[type="button"]:hover,
    input[type="submit"]:hover,
    input[type="reset"]:hover,
     {
        background: #cccccc;
    }

    input[type="button"].style2,
    input[type="submit"].style2,
    input[type="reset"].style2,
    button.style2,
    .button.style2 {
        color: #3a3939;
        background: #fff;
        box-shadow: inset 0 0 0 1px #fff;
    }

        input[type="button"].style2:hover,
        input[type="submit"].style2:hover,
        input[type="reset"].style2:hover,
        button.style2:hover,
        .button.style2:hover {
            background: rgba(255, 255, 255, 0.1);
            color: #fff !important;
            text-shadow: 0 0 0.5px rgba(255, 255, 255, 0.25) !important;
        }

    input[type="button"].style3,
    input[type="submit"].style3,
    input[type="reset"].style3,
    button.style3,
    .button.style3 {
        background: none;
        color: #3a3939;
        box-shadow: inset 0 0 0 1px #dad9d9;
    }

        input[type="button"].style3:hover,
        input[type="submit"].style3:hover,
        input[type="reset"].style3:hover,
        button.style3:hover,
        .button.style3:hover {
            background: rgba(58, 57, 57, 0.025);
        }

/* Box */

	.box {
		position: relative;
		margin-top: 9em;
		margin-bottom: 0;
	}

		.box:after {
			content: '';
			display: block;
			position: absolute;
			top: -9em;
			left: 50%;
			height: 9em;
			border-left: solid 1px #fff;
		}

		.box:before {
			content: '';
			display: block;
			width: 90px;
			height: 66px;
			position: absolute;
			left: 50%;
			top: -4.5em;
			margin-left: -45px;
			margin-top: -33px;
			/* background: url("images/arrow.svg") no-repeat; */
		}

		.box :last-child {
			margin-bottom: 0;
		}

		.box.style1 {
			/* background-image: url("images/agsquare.png"); */
			padding: 3em;
		}

			.box.style1 .image {
				display: block;
				position: absolute;
				top: 0;
				width: 50%;
				height: 100%;
				background: #152E33;
				overflow: hidden;
			}

				.box.style1 .image img {
					position: absolute;
					top: 0;
					height: 100%;
				}

			.box.style1 .inner > :last-child {
				margin: 0;
			}

			.box.style1.right .image {
				left: 0;
			}

				.box.style1.right .image img {
					right: 0;
				}

			.box.style1.right .inner {
				margin-left: 50%;
				padding-left: 3em;
			}

			.box.style1.left .image {
				right: 0;
			}

				.box.style1.left .image img {
					left: 0;
				}

			.box.style1.left .inner {
				/* margin-right: 50%; */
				padding-right: 3em;
			}

		.box.style2 {
			text-align: center;
		}

			.box.style2 header {
				display: inline-block;
				/* background-image: url("images/agsquare.png"); */
				padding: 2em 3em 2em 3em;
				margin: 0;
			}

				.box.style2 header p {
					padding: 0;
				}

			.box.style2 .inner {
				position: relative;
				padding: 40px 0 0px 0;
			}

				.box.style2 .inner:after {
					content: '';
					display: block;
					position: absolute;
					top: 0;
					left: 50%;
					height: 100%;
					border-left: solid 1px #fff;
				}

				.box.style2 .inner .row {
					position: relative;
				}

					.box.style2 .inner .row:before {
						content: '';
						display: block;
						position: absolute;
						top: 50%;
						left: 20px;
						width: calc(100% - 40px);
						border-bottom: solid 1px #fff;
					}

				.box.style2 .inner .image {
					position: relative;
					z-index: 1;
					padding: 20px;
				}

		.box.style3 {
			background-image: url("/images/agsquare.png");
			padding: 3em;
		}

			.box.style3 header {
				text-align: center;
			}

/* Icons */

	.icon {
		text-decoration: none;
	}

		.icon:before {
			display: inline-block;
			font-family: FontAwesome;
			font-size: 1.25em;
			text-decoration: none;
			font-style: normal;
			font-weight: normal;
			line-height: 1;
			-webkit-font-smoothing: antialiased;
			-moz-osx-font-smoothing: grayscale;
		}

		.icon > .label {
			display: none;
        }

/* Mobile */

@media screen and (max-width: 736px) {

        input, textarea, select {
            line-height: 1.5em;
            font-size: 12pt;
            letter-spacing: 0;
        }

    /* Actions */

        ul.actions li {
            display: block;
            margin: 1em 0 0 0;
        }

            ul.actions li:first-child {
                margin-top: 0;
            }

    /* Box */

        .box {
            margin-top: 4.5em;
        }

            .box:after {
                top: -4.5em;
                height: 4.5em;
            }

            .box:before {
                width: 45px;
                height: 33px;
                top: -2.25em;
                margin-left: -22.5px;
                margin-top: -16.5px;
                background-size: 45px 33px;
            }

            .box.style1 {
                padding: 0;
                text-align: center;
            }

                .box.style1 .image {
                    position: relative !important;
                    left: 0 !important;
                    width: 100% !important;
                    height: auto !important;
                }

                    .box.style1 .image img {
                        position: relative;
                        height: auto;
                        width: 100%;
                    }

                .box.style1 .inner {
                    margin: 0 !important;
                    padding: 2em 1.25em 2em 1.25em !important;
                }

            .box.style2 .inner .row:before {
                display: none;
            }

            .box.style2 .inner .image {
                padding: 0;
                max-width: 250px;
                margin: 0 auto 20px auto;
            }

            .box.style3 {
                padding: 2em 1.25em 2em 1.25em;
            }

}
